.Grid {
  display: grid;
  padding: 8px;
  grid-template-columns: repeat(auto-fill, minmax(min(160px, 100%), 1fr));
  gap: 1rem;
}

.Card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
}

.Card img {
  width: 164px;
  height: 164px;
  border-radius: 16px;
  object-fit: contain;
  background-color: lightgray;
}

.Card .Description {
  font-size: small;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* number of lines to show */
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

.Card .PurchaseInfo {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
}

.Card .PurchaseInfo .Price {
  display: flex;
  flex-direction: row;
  gap: 4px;
}

.Card .PurchaseInfo .Price .Sum {
  font-size: medium;
}

.Card .PurchaseInfo .Price .Currency {
  font-size: medium;
}
